/* === Plugin Overrides === */

/* Avgrund Popup */

.avgrund-popin {
  position: absolute;
  background: #fff;
  padding: 37px 33px;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  top: 50%;
  left: 50%;
  z-index: 10000;
  font-size: 0.875rem;
  font-family: "Roboto", sans-serif;
  font-weight: initial;
  line-height: 1.85;
  border-radius: 10px;
  transform: scale(0.8);
}

.avgrund-popin p {
  font-size: 0.875rem;
  font-family: "Roboto", sans-serif;
  font-weight: initial;
}

.avgrund-overlay {
  background: #000;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1030;
  visibility: hidden;
  opacity: 0;
  filter: alpha(opacity=0);
}

.avgrund-ready .avgrund-overlay,
.avgrund-ready .avgrund-popin,
body.avgrund-ready {
  transform-origin: 50% 50%;
  transition: 0.3s all ease-out;
}

body.avgrund-active {
  overflow: hidden;
}

.avgrund-active .avgrund-popin {
  visibility: visible;
  opacity: 1;
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  transform: scale(1.1);
}

@media (max-width: 767px) {
  .avgrund-active .avgrund-popin {
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
    transform: scale(0.8);
  }
}

.avgrund-active .avgrund-overlay {
  visibility: visible;
  opacity: 0.5;
  height: 20000px;
}

.avgrund-popin.stack {
  transform: scale(1.5);
}

.avgrund-active .avgrund-popin.stack {
  transform: scale(1.1);
}

/* Optional close button styles */

.avgrund-close {
  display: block;
  color: #fff;
  background: #343a40;
  font-size: 10px;
  text-decoration: none;
  text-transform: uppercase;
  position: absolute;
  top: 10px;
  right: 10px;
  height: 17px;
  width: 17px;
  text-align: center;
  line-height: 16px;
  border-radius: 50px;
}

.avgrund-close:hover {
  color: #fff;
  text-decoration: none;
}

